body {
  color: #fff;
  --text-color: white;
  --background-color: #333;
  --game-text-color: black;
  background-image: url("../congruent_outline.c020253a.png");
  margin: 0;
  padding: 0;
  font-family: "Press Start 2P", -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  animation: 60s ease-out infinite pizzaz;
}

@keyframes pizzaz {
  0% {
    background-position: 0 0;
  }

  25% {
    background-position: 200% 200%;
  }

  50% {
    background-position: 400% -200%;
  }

  75% {
    background-position: 0 100%;
  }

  100% {
    background-position: 0 0;
  }
}

a {
  color: #0af;
}

#game-container {
  align-items: stretch;
  display: flex;
}

#gameplay-area {
  width: 100vw;
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

#game-screen {
  color: #000;
  height: 100vh;
  background-color: #333;
  justify-content: center;
  align-items: center;
  display: flex;
}

#game-canvas {
  width: 100vw;
  height: 56.25vw;
  max-height: 100vh;
  max-width: 177.778vh;
}

#credit-display {
  min-height: 100vh;
  box-sizing: border-box;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  padding: 50px;
  display: flex;
}

button {
  min-width: 50px;
  color: #fff;
  background-color: #0000;
  border: 2px solid #fff;
  outline: none;
  margin: 1em;
  padding: 1em;
  font-family: inherit;
}

input[type="text"] {
  color: #fff;
  background-color: #0000;
  border: 2px solid #fff;
  border-radius: 3px;
  margin: 1em;
  padding: .5em;
}

button.selected, button:hover, button:active {
  min-width: 50px;
  color: #000;
  background-color: #fff;
  outline: none;
  padding: 1em;
  font-family: inherit;
}

#game-menu, #game-over-view, #pause-view, #high-scores-display, .prompt-screen, #controls-menu, noscript {
  height: 100vh;
  color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.score-entry {
  margin: 1em;
}

#game-over-view, #pause-view {
  background-color: #000000b3;
}

.hidden {
  display: none !important;
}

.prompt-screen {
  box-sizing: border-box;
  width: 100vw;
  height: 100vh;
  color: #fff;
  background-color: #000000b3;
  padding: 3em;
  position: fixed;
  top: 0;
  left: 0;
}

.row {
  flex-direction: row;
  display: flex;
}

#game-overlay {
  height: 100vh;
  width: 100vw;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
}

#game-overlay > * {
  pointer-events: all;
}

#volume-toggle {
  z-index: 10000;
  color: #fffc;
  padding: .75em;
  position: fixed;
  top: 0;
  right: 0;
}

#volume-toggle > .volume-on, #volume-toggle.on:hover > .volume-on {
  display: none;
}

#volume-toggle.on > .volume-on, #volume-toggle:hover > .volume-on {
  display: inline-block;
}

#volume-toggle > .volume-off {
  margin: 0 .625em 0 0;
}

#volume-toggle > .volume-off, #volume-toggle.on:hover > .volume-off {
  display: inline-block;
}

#volume-toggle.on > .volume-off, #volume-toggle:hover > .volume-off {
  display: none;
}

